import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';

import type { ButtonVariant, ButtonSize } from './my-button';

const meta: Meta = {
  title: 'Components/MyButton',
  component: 'my-button',
  tags: ['autodocs'],
  argTypes: {
    label: {
      control: 'text',
      description: 'The text to display inside the button',
    },
    variant: {
      control: 'select',
      options: ['primary', 'secondary', 'outline'] as ButtonVariant[],
      description: 'The visual variant of the button',
    },
    size: {
      control: 'radio',
      options: ['small', 'medium', 'large'] as ButtonSize[],
      description: 'The size of the button',
    },
    disabled: {
      control: 'boolean',
      description: 'Whether the button is disabled',
    },
    myClick: {
      action: 'myClick event',
      description: 'Event emitted when the button is clicked',
    },
  },
  render: (args) => html`
    <my-button
      label="${args.label}"
      variant="${args.variant}"
      size="${args.size}"
      ?disabled="${args.disabled}"
      @myClick=${args.myClick}
    ></my-button>
  `,
};

export default meta;

// --- Stories ---

export const Primary: StoryObj = {
  args: {
    label: 'Primary Button',
    variant: 'primary',
    size: 'medium',
    disabled: false,
  },
};

export const Secondary: StoryObj = {
  args: {
    label: 'Secondary Button',
    variant: 'secondary',
    size: 'medium',
    disabled: false,
  },
};

export const Outline: StoryObj = {
  args: {
    label: 'Outline Button',
    variant: 'outline',
    size: 'medium',
    disabled: false,
  },
};

export const Small: StoryObj = {
  args: {
    label: 'Small Button',
    variant: 'primary',
    size: 'small',
    disabled: false,
  },
};

export const Large: StoryObj = {
  args: {
    label: 'Large Button',
    variant: 'primary',
    size: 'large',
    disabled: false,
  },
};

export const Disabled: StoryObj = {
  args: {
    label: 'Disabled Button',
    variant: 'primary',
    size: 'medium',
    disabled: true,
  },
};

export const Clickable: StoryObj = {
  args: {
    label: 'Click Me',
    variant: 'primary',
    size: 'medium',
    disabled: false,
  },
};
